<template>
    <div class="returnDetail" ref="wrapper" v-loading="loading" element-loading-background="rgba(255, 255, 255, 0.2)">
        <el-table :data="list" :height="tableHeight" highlight-current-row border style="width: 100%">
            <el-table-column prop="createTime" label="" :formatter="formatter" width="180"></el-table-column>
            <el-table-column prop="earningTime" label="" :formatter="formatterEarningTime" :render-header="earningTimeLabel" width="180"></el-table-column>
            <el-table-column prop="auctionTitle" label="" width="400"></el-table-column>
            <el-table-column prop="feeString" label="" :render-header="feeStringLabel"></el-table-column>
            <el-table-column prop="finalfee" label="" :render-header="finalfeeLabel"></el-table-column>
            <el-table-column prop="service_fee" label="7" :render-header="service_feeLabel"></el-table-column>
            <el-table-column prop="tax_fee" label="" :render-header="tax_feeLabel"></el-table-column>
            <el-table-column prop="tb_service_fee" label="" :render-header="tb_service_feeLabel"></el-table-column>
        </el-table>
        <div ref="pagination" class="paging">
			<a @click="pageTurning(-1)" class="jump" href="javascript:void(0)"></a>
			<a class="jump" href="javascript:void(0)" v-text="pageNo"></a>
			<a @click="pageTurning(1)" class="jump" href="javascript:void(0)"></a>
			<a class="jumpTo" href="javascript:void(0)"></a>
			<input @keyup.enter="jumpTo" class="jump page-num" v-model="jumpText">
			<a @click="jumpTo" class="jump" href="javascript:void(0)">GO</a>
		</div>
    </div>
</template>

<script>
    import { mapActions } from 'vuex'
    import Jq from 'jquery'
    import Filter from '../lib/filter'
    import toStorage from '../lib/storage'
    export default {
        data() {
            return {
                tableHeight: null,
                list: [],
                loading: false,
                user_id: '',
                pageNo: 1,
                pageSize: 15,
                jumpText: '',
                isNext: true
            }
        },
        watch: {
			pageNo: function() {
				this.getList();
			}
		},
        created: function() {
            this.changeIndex(3);
            this.user_id = toStorage('user_id') || '';
        },
        mounted: function() {
            this.$nextTick(function() {
                this.tableHeight = document.body.clientHeight - this.$refs.pagination.getBoundingClientRect().height - 160;
                this.getList();
            })
        },
        methods: {
            ...mapActions([
                'changeIndex'
            ]),
            pageTurning: function(val) {
				switch (val) {
					case -1:
					this.pageNo--;
					if (this.pageNo < 1) {
						this.pageNo = 1
					}
					break;
					case 1:
                    if (this.isNext) {
                        this.pageNo++;
                    }
					break;
				}
			},
			jumpTo: function() {
				this.pageNo = this.jumpText;
			},
            getList: function() {
                this.loading = true;
                this.list = [];
                var _this = this;
                Jq.ajax({
                    url: 'https://7fanli.com:8443/Seven/fanli/incomeDetail.do',
                    type: 'GET',
                    data: {
                        'user_id': _this.user_id,
                        'pageNo': _this.pageNo,
                        'pageSize': _this.pageSize
                    },
                    dataType: 'jsonp',
                    success: function(res) {
                        _this.list = res;
                        _this.loading = false;
                        if (res.length < _this.pageSize) {
                            _this.isNext = false;
                        } else {
                            _this.isNext = true;
                        }
                    }
                })
            },
            formatter: function(row) {
                return Filter.formatCreateTime(row.createTime);
            },
            formatterEarningTime: function(row) {
                return Filter.formatEarnTime(row.earningTime);
            },
            earningTimeLabel(h) {
                return (
                    <el-tooltip class="item" effect="light" content="2009011020" placement="top-start">
                        <el-button></el-button>
                    </el-tooltip>
                )
            },
            feeStringLabel(h) {
                return (
                    <el-tooltip class="item" effect="light" content="" placement="top-start">
                        <el-button></el-button>
                    </el-tooltip>
                )
            },
            finalfeeLabel(h) {
                return (
                    <el-tooltip class="item" effect="light" content=" 20" placement="top-start">
                        <el-button></el-button>
                    </el-tooltip>
                )
            },
            service_feeLabel(h) {
                return (
                    <el-tooltip class="item" effect="light" content="77" placement="top-start">
                        <el-button>7</el-button>
                    </el-tooltip>
                )
            },
            tax_feeLabel(h) {
                return (
                    <el-tooltip class="item" effect="light" content="" placement="top-end">
                        <el-button></el-button>
                    </el-tooltip>
                )
            },
            tb_service_feeLabel(h) {
                return (
                    <el-tooltip class="item" effect="light" content="10%20" placement="top-end">
                        <el-button></el-button>
                    </el-tooltip>
                )
            }
        }
    }
</script>

<style lang="less" src="../style/returnDetail.less"></style>


