<!--  -->
<template>
	<div class="loginBoxCenter">
		<p>&nbsp;<span  v-if="errFlag" class="errSpan"></span></p>
		<p><label for="username"></label>
		<!--autofocus  -->
		<!-- placeholder-->
		<input type="text" id="text" name="text" class="loginInput" autofocus="autofocus" required="required" autocomplete="off" placeholder="" v-model="user_tel" /></p>
		<!-- required -->
		<p><label for="password">&nbsp;&nbsp;&nbsp;</label>
		<input type="password" id="password" name="password" class="loginInput" required="required" placeholder="" v-model="password" @keyup.enter="login" /></p>
		<p><button class="loginBtn" v-on:click="login"></button></p>
		<span v-if="false" >
		<input id="remember" type="checkbox" name="remember" />
		<label  for="remember"></label>
		</span>
	</div>
</template>
 
<script>
	import {mapState,mapActions} from 'vuex'
//  
export default {
	name: 'loginlogin',
	data() {
            return {
                user_tel: '',
                errFlag: false,
				password: ''
            }
        },
	methods: {
            login: function() {
                this.$http.jsonp(this.web_path+'scoupon/gg/passwdLogin.do?user_tel='+this.user_tel+'&password='+this.password)
                .then(function(res) {
					var flag = res.body.flag;
					if(flag=='true' || flag==true) {
						window.location.href = '#/app1';
					} else {
						this.errFlag = true;
					}
                })
            },
        },
	computed:
		mapState(['web_path'])
}
</script>
<style lang="less" src="../style/login.less"></style>




