<template>
    <div class="drawMoney-content">
        <div class="wxtip">2122</div>
        <div class="el-row-box">
            <el-row :gutter="20">
                <el-col :span="6">
                    <div class="grid-content bg-purple">
                        <ul class="block">
                            <li><img src="../assets/all.png" alt=""></li>
                            <li>
                                <div>
                                    <span></span>
                                    <el-tooltip class="item" effect="light" content="7" placement="top-start">
                                        <el-button><img src="../assets/wh.png" alt=""></el-button>
                                    </el-tooltip>
                                </div>
                                <div><span class="nums" v-text="total_amt"></span></div>
                            </li>
                        </ul>
                    </div>
                </el-col>
                <el-col :span="6">
                    <div class="grid-content bg-purple">
                        <ul class="block">
                            <li><img src="../assets/candraw.png" alt=""></li>
                            <li>
                                <div>
                                    <span></span>
                                    <el-tooltip class="item" effect="light" content="22" placement="top-start">
                                        <el-button><img src="../assets/wh.png" alt=""></el-button>
                                    </el-tooltip>
                                </div>
                                <div><span class="nums" v-text="balance"></span></div>
                            </li>
                        </ul>
                    </div>
                </el-col>
                <el-col :span="6">
                    <div class="grid-content bg-purple">
                        <ul class="block">
                            <li><img src="../assets/drawall.png" alt=""></li>
                            <li>
                                <div>
                                    <span></span>
                                    <el-tooltip class="item" effect="light" content="7" placement="top-end">
                                        <el-button><img src="../assets/wh.png" alt=""></el-button>
                                    </el-tooltip>
                                </div>
                                <div><span class="nums" v-text="draw_money"></span></div>
                            </li>
                        </ul>
                    </div>
                </el-col>
                <el-col :span="6">
                    <div class="grid-content bg-purple">
                        <ul class="block">
                            <li><img src="../assets/drawing.png" alt=""></li>
                            <li>
                                <div>
                                    <span></span>
                                    <el-tooltip class="item" effect="light" content="24" placement="top-end">
                                        <el-button><img src="../assets/wh.png" alt=""></el-button>
                                    </el-tooltip>
                                </div>
                                <div><span class="nums" v-text="total_task_money"></span></div>
                            </li>
                        </ul>
                    </div>
                </el-col>
            </el-row>
        </div>
        <div class="draw-do">
            <ul>
                <li class="d-text">22</li>
                <li class="d-text"></li>
                <li class="d-input"><el-input v-model="drawNum" placeholder=""></el-input></li>
                <li class="d-tips"><el-alert v-show="isSHowAlert" :title="title" :type="type"></el-alert></li>
                <li class="d-btn"><el-button @click="drawMoney" type="success" plain> </el-button></li>
            </ul>
        </div>
        <el-dialog title="" :visible.sync="isShowDialog" width="25%" center> 
            <div>
                <el-tag type="info"></el-tag>
                <el-tag class="label" type="info" v-text="real_name"></el-tag>
            </div>
            <div>
                <el-tag type="info"></el-tag>
                <el-tag class="label" type="info" v-text="alipay_user_name"></el-tag>
            </div>
            <div><el-tag type="info"></el-tag></div>
            <span slot="footer" class="dialog-footer">
                <el-button type="success" plain @click="toUserCenter"></el-button>
                <el-button type="success" @click="toDraw"></el-button>
            </span>
        </el-dialog>

        <!--<div class="layer">
            <el-dialog title="7" :visible.sync="daysDialog" width="30%">
                <p></p>
                <p class="text">22212242257</p>
                <span slot="footer" class="dialog-footer">
                    <el-button type="success" plain @click="daysDialog = false"></el-button>
                    <el-button type="success" plain @click="IKnow"></el-button>
                </span>
            </el-dialog>
        </div>-->
    </div>
</template>

<script>
    import Jq from 'jquery'
    import { mapGetters,mapActions } from 'vuex'
    import toStorage from '../lib/storage'
    export default {
        data () {
            return {
                total_amt: 0,
                balance: 0,
                draw_money: 0,
                total_task_money: 0,
                drawNum: '',
                title: '',
                type: '',
                isSHowAlert: false,
                isShowDialog: false,
                alipay_user_name: '',
                real_name: '',
                // daysDialog: false
            }
        },
        created: function() {
            this.changeIndex(1)
        },
        mounted: function() {
            this.$nextTick(function() {
                this.alipay_user_name = toStorage('alipay_user_name');
                this.real_name = toStorage('real_name');
                this.getAmtInfo();
                // if (!toStorage('iknow')) {
                //     this.daysDialog = true
                // }
            })
        },
		computed: {
			...mapGetters([
				'web_path'
			])
		},
        methods: {
            ...mapActions([
                'changeIndex'
            ]),
            getAmtInfo: function() {
                var _this = this;
                Jq.ajax({
                    url: this.web_path+'scoupon/user/getAmtInfo.do',
                    type: 'GET',
                    dataType: 'jsonp',
                    success: function(res) {
                        if (res.flag == 'false') {
                        } else {
                            _this.total_amt = res.total_amt.toFixed(2);
                            _this.balance = res.balance.toFixed(2);
                            _this.draw_money = res.draw_money.toFixed(2);
                            _this.total_task_money = res.total_task_money.toFixed(2);
                        }
                    }
                })
            },
            drawMoney: function() {
                if (!this.alipay_user_name || !this.real_name) {
                    this.alertTip('', 'info')
                } else if (!this.drawNum || Number(this.drawNum) <= 0) {
                    this.alertTip('', 'info')
                } else if (Number(this.drawNum) > Number(this.balance)) {
                    this.alertTip(''+this.balance+'', 'info')
                } else if (!this.checkNum()) {
                    this.alertTip(''+this.balance+'', 'info')
                } else {
                    this.isShowDialog = true;
                }
            },
            toUserCenter: function() {
                this.$router.push('userCenter')
            },
            toDraw: function() {
                this.isShowDialog = false;
                var _this = this;
                Jq.ajax({
                    url: this.web_path+'scoupon/user/payTastSave.do',
                    type: 'GET',
                    data: {
                        'money': _this.drawNum
                    },
                    dataType: 'jsonp',
                    success: function(res) {
                        if(res.count == 1){
                            _this.alertTip('', 'success');
                        }else if (res.count == 0) {
                            _this.alertTip(res.msg, 'info');
                        }
                    }
                })
            },
            alertTip: function(title, type) {
                this.title = title;
                this.type = type;
                this.isSHowAlert = true;
            },
            checkNum: function() {
                if(!(/^\d+(\.\d+)?$/.test(this.drawNum))){
                    return false;
                }
                return true;
            }
        }
    }
</script>

<style lang="less" src="../style/drawMoney.less"></style>


